import * as yup from "yup";


export const createStoreSchema = yup.object().shape({
    name: yup
        .string()
        .max(255,'Ti a 255 k t')
        .trim()
        .required("Khng c  trng"),
    address: yup
        .string()
        .max(255,'Ti a 255 k t')
        .trim()
        .required("Khng c  trng"),
    phone: yup
        .string()
        .matches(/(84|0[3|5|7|8|9])+([0-9]{8})\b/g, 'khng ng nh dng')
        .required("Khng c  trng"),
});

const yesterday = new Date((new Date()).valueOf() - 1000 * 60 * 60 * 24);
export const couponSchema = yup.object().shape({
    name: yup
        .string()
        .max(255,'Ti a 255 k t')
        .trim()
        .required("Khng c  trng"),
    sku: yup
        .string()
        .max(255,'Ti a 255 k t')
        .trim()
        .required("Khng c  trng"),
    description: yup
        .string()
        .max(255,'Ti a 255 k t')
        .trim(),
    price: yup
        .number()
        .min(0, "khng c nh hn 0")
        .typeError('Phi l s')
        .required("Khng c  trng"),
    condition: yup
        .number()
        .min(0, "khng c nh hn 0")
        .typeError('Phi l s')
        .required("Khng c  trng"),
    date_begin: yup
        .date()
        .required("Khng c  trng")
        .min(yesterday, 'khng c nh hn ngy hm nay'),
    hide: yup
        .string()
        .max(255,'Ti a 255 k t'),
    date_end: yup
        .date()
        .required("Khng c  trng")
        .min(
            yup.ref('date_begin'),
            "phi ln hn ngy bt u"
        )
});